import { Node, SettingType} from '../../node';
import { Container } from '../../container';
import { Type } from '../../node-io';
import TimeUtils, { TIME_TYPE } from '../../utils/time-utils';

class DelayOffNode extends Node {
  lastInput:boolean;
  lastReset:boolean;
  timeoutFunc;
  enabled = false;
  remainingFunc;
  timeUnits;
  remainingUpdateMillis;
  remainingUpdateSize;


  constructor() {
    super();
    this.title = 'Off Delay';
    this.description =
      "output matches input, but output transitions from 'true' to 'false' are delayed by 'delay' duration. The delay is cancelled when 'reset' transitions from 'false' to 'true. offDelay active is true during offDelay periods. 'delay' units can be configured from settings.  Maximum delay setting is 587 hours. (See Figure A.)";
    this.addInput('input', Type.BOOLEAN);
    this.addInputWithSettings('delay', Type.NUMBER, 1, 'Off Delay Duration (Max 597 Hours)');
    this.addInput('reset', Type.BOOLEAN);
    this.addOutput('output', Type.BOOLEAN);
    this.addOutput('offDelay active', Type.BOOLEAN);
    this.addOutput('remaining', Type.NUMBER);

    this.settings['time'] = {
      description: 'Units',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: TIME_TYPE.MILLISECONDS, text: 'Milliseconds' },
          { value: TIME_TYPE.SECONDS, text: 'Seconds' },
          { value: TIME_TYPE.MINUTES, text: 'Minutes' },
          { value: TIME_TYPE.HOURS, text: 'Hours' },
        ],
      },
      value: TIME_TYPE.SECONDS,
    };
    this.setSettingsConfig({
      groups: [{ delay: { weight: 2 }, time: {} }],
    });
    
  }

  onCreated() {
    this.setOutputData(0, false);
    this.setOutputData(1, false);
    this.setOutputData(2, 0);
    this.lastInput = false;
    this.lastReset = false;
  }

  onAdded() {
 
    this.onAfterSettingsChange();
  }

  onAfterSettingsChange() {
    this.timeUnits = this.settings['time'].value;
    this.inputs[1]['name'] = `[delay] (${this.timeUnits})`;
    this.outputs[2]['name'] = `[remaining] (${this.timeUnits})`;
    this.onInputUpdated();
  }

  onExecute(){
    //this.onInputUpdated();
  }

  onInputUpdated() {
    const delay = this.getInputData(1);
    const delayMilli = TimeUtils.timeConvert(delay, this.timeUnits);

    let input = this.getInputData(0);
    if (input !== true) input = false;

    const reset = this.getInputData(2);
    if (reset && this.inputs[2].updated && !this.lastReset && this.enabled) {
      this.clearTimers();
      this.setOutputData(0, input);
      this.setOutputData(1, false);
      this.setOutputData(2, 0);
      this.enabled = false;
    }
    this.lastReset = reset;

    if (!input && this.inputs[0].updated && this.lastInput) {
      this.setOutputData(1, true);
      this.setOutputData(2, delay);
      this.clearTimers();
      this.enabled = true;
      this.timeoutFunc = setTimeout(() => {
        this.enabled = false;
        this.setOutputData(0, false);
        this.setOutputData(1, false);
        this.setOutputData(2, 0);
        clearInterval(this.remainingFunc);
      }, delayMilli);
      switch(this.timeUnits){
        case TIME_TYPE.MILLISECONDS:
          this.remainingUpdateMillis = 500;
          this.remainingUpdateSize = 500;
          break;
        case TIME_TYPE.SECONDS:
          this.remainingUpdateMillis = 1000;
          this.remainingUpdateSize = 1;
          break;
        case TIME_TYPE.MINUTES:
          this.remainingUpdateMillis = 6000;
          this.remainingUpdateSize = 0.1;
          break;
        case TIME_TYPE.HOURS:
          this.remainingUpdateMillis = 360000;
          this.remainingUpdateSize = 0.1;
          break;
      }
      this.remainingFunc = setInterval(() => {
        const remaining = this.outputs[2].data - this.remainingUpdateSize;
        this.setOutputData(2, remaining);
      }, this.remainingUpdateMillis);
    }
    this.lastInput = input;
    if (input) {
      this.setOutputData(0, true);
      this.setOutputData(1, false);
      this.setOutputData(2, 0);
      if (this.inputs[0].updated && this.enabled) {
        this.clearTimers();
        this.enabled = false;
      }
    }
  }

  private clearTimers() {
    if (this.timeoutFunc) clearTimeout(this.timeoutFunc);
    if (this.remainingFunc) clearInterval(this.remainingFunc);
  }
}
Container.registerNodeType('boolean/off-delay', DelayOffNode);
